

































































































































































































































































import Vue from "vue";
import User from "../models/user";

export default Vue.extend({
    name: "User",
    data() {
        return {
            isLoading: false,
            isCreating: false,
            isSaving: false,
            user: null,
        };
    },
    computed: {
        users() {
            return this.$store.state.users.all;
        },
    },
    created() {
        this.loadUser();
    },
    methods: {
        loadUser() {
            this.isLoading = true;
            this.$store
                .dispatch("users/load")
                .catch(() => {
                    this.$snotify.error("User konnten nicht geladen werden");
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
        createUser() {
            this.user = new User();
            this.isCreating = true;
        },
        isEditMode(user): boolean {
            return this.user === user;
        },
        addUser() {
            this.isSaving = true;
            this.$store
                .dispatch("users/add", this.user)
                .then(() => {
                    this.$snotify.success("Der Benutzer wurde hinzugefügt.");
                })
                .catch(() => {
                    this.$snotify.error(
                        "Der Benutzer konnte nicht hinzugefügt werden!"
                    );
                })
                .finally(() => {
                    this.isSaving = false;
                    this.isCreating = false;
                });
        },
        editUser(user) {
            this.user = user;
        },
        updateUser(user) {
            this.isSaving = true;
            this.$store
                .dispatch("users/update", user)
                .then(() => {
                    this.$snotify.success("Die Änderungen wurden gespeichert!");
                })
                .catch(() => {
                    this.$snotify.error(
                        "Die Änderungen konnten nicht gespeichert werden!"
                    );
                })
                .finally(() => {
                    this.isSaving = false;
                });
        },
        revokePassword(user) {
            if (
                window.confirm(
                    "Soll das Passwort wirklich zurückgesetzt werden?"
                )
            ) {
                this.isSaving = true;
                this.$store
                    .dispatch("users/revoke", user)
                    .then(() => {
                        this.$snotify.success(
                            "Das Passwort wurde zurückgesetzt!"
                        );
                    })
                    .catch(() => {
                        this.$snotify.error(
                            "Das Passwort konnte nicht zurückgesetzt werden!"
                        );
                    })
                    .finally(() => {
                        this.isSaving = false;
                    });
            }
        },
        deleteUser(user) {
            if (window.confirm("Soll der User wirklich gelöscht werden?")) {
                this.isSaving = true;
                this.$store
                    .dispatch("users/delete", user)
                    .then(() => {
                        this.$snotify.success("Der User wurde gelöscht!");
                    })
                    .catch(() => {
                        this.$snotify.error(
                            "Der User konnte nicht gelöscht werden!"
                        );
                    })
                    .finally(() => {
                        this.isSaving = false;
                    });
            }
        },
        cancelEditMode() {
            this.user = null;
        },
    },
});
